.main {
  display: flex;
  justify-content: space-around; }

.main-header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  height: 48px;
  z-index: 8;
  background-color: #f3f3f3; }

.list {
  width: 16%;
  padding-bottom: 40px; }

.header {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 48px;
  padding: 8px;
  z-index: 9;
  color: #7e8e99;
  background-color: #f3f3f3; }
  .header h2 {
    font-size: 20px; }

.desc {
  font-size: 14px;
  color: #7e8e99; }

.today {
  margin-right: -8px;
  padding: 0 8px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  color: #000000;
  font-weight: 500;
  font-size: 14px;
  background-color: #fcc800;
  border-radius: 12px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }

.init {
  margin: 12px 0;
  text-align: center;
  font-size: 14px;
  color: #7e8e99; }

@media (max-width: 1024px) {
  .list {
    width: 19%; }
    .list:nth-last-child(1) {
      display: none; }
  .header {
    padding-left: 0; }
    .header h2 {
      font-size: 18px; }
  .today {
    padding: 0 4px;
    height: 20px;
    line-height: 20px;
    font-size: 12px; } }

@media (max-width: 768px) {
  .list {
    width: 24%; }
    .list:nth-last-child(2) {
      display: none; } }
