.picker {
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  padding-left: 16px;
  height: 48px;
  font-weight: 500;
  font-size: 14px;
  background-color: #f3f3f3;
  z-index: 9; }

.current {
  display: flex;
  align-items: center;
  flex: 1;
  line-height: 32px;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }
  .current[data-opend="true"] svg {
    transform: rotate(-180deg); }
  .current img {
    margin-right: 8px;
    width: 20px;
    height: 20px;
    border-radius: 4px;
    overflow: hidden; }
  .current svg {
    width: 16px;
    height: 16px;
    transition: transform 200ms ease-in-out; }

.dropdown {
  position: absolute;
  top: calc(48px - 8px);
  right: 0;
  left: 16px;
  padding: 8px 0;
  background-color: #fff;
  z-index: 10;
  opacity: 0;
  border-radius: 8px;
  transition: all 200ms ease-in-out;
  pointer-events: none;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.16); }
  .dropdown[data-opend="true"] {
    opacity: 1;
    pointer-events: auto; }

.city {
  padding-left: 28px;
  line-height: 32px;
  color: #7e8e99;
  transition: all 100ms ease-in-out;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }
  .city:hover {
    color: #000000;
    background-color: #fcc800; }
  .city[data-current="true"] {
    display: none; }
