.course {
  position: relative;
  margin: 8px 0 12px;
  padding: 4px 8px;
  line-height: 1.25;
  background-color: #fff;
  border-radius: 8px;
  transition: all 200ms ease-in-out; }
  .course:hover {
    box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.16);
    transform: scale(1.2);
    z-index: 1; }

.header {
  display: flex;
  justify-content: space-between;
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 12px;
  color: #7e8e99;
  line-height: 20px; }

.time {
  font-weight: 500; }

.price[data-cheap="true"] {
  color: #fcc800;
  font-weight: 500; }

.title {
  font-weight: normal;
  font-size: 14px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }

.info {
  display: flex;
  align-items: center;
  font-size: 12px;
  color: #7e8e99; }

.avatar {
  margin-right: 8px;
  min-width: 20px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  overflow: hidden; }

.info-main {
  flex: 1;
  transform: scale(0.8);
  transform-origin: left;
  overflow: hidden; }

.tags {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }
